/**************** common-class ********************/
:root {
  --size: 10px;
  --clr1: #0df7ff;
  --clr2: #00b4d6;
  --clr3: #2f75ff;
  --clr4: #162da1;
  --clr5: #031053;
  --hl1: #ff0002;
  --ico-s1: 6.4rem;
  --ico-s2: 2rem;
}

//텍스트 안보이게
.line-cut {
  overflow: hidden;
}

.no-click {
  pointer-events: none;
}

.pt-60 {
  padding-top: 6rem !important;
}

/*검색 위치 조정*/
.new-pt {
  padding-top: 6rem !important;
}
//ellipsis
.ell {
  display: block;
  width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  word-break: break-all;
}
.ell-1 {
  display: block;
  display: -webkit-box !important;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  word-break: break-all;
}
.ell-2 {
  display: block;
  display: -webkit-box !important;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  word-break: break-all;
}
.ell-3 {
  display: block;
  display: -webkit-box !important;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  word-break: break-all;
}

//scroll
.no-scr {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.noScr,
.scr-x {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
//.scr-x {display:inline-flex; width:100%; white-space:nowrap; justify-content:flex-start; overflow-x:auto; overflow-y: hidden;}
.scr-x:-webkit-scrollbar {
  display: none;
  width: 0; /* Remove scrollbar space */
  height: 0;
  background: transparent; /* Optional: just make scrollbar invisible */
  -webkit-appearance: none;
}

