//버튼
[class*="btn-type-"] {
  position: relative;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  display: inline-block;
  border-width: 1px;
  border-style: solid;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  text-decoration: none;
  font-weight: 600;
  &.btn-type-a {
    padding: 0 1rem;
    color: #000;
    border: 1px solid #000;
    &:hover {
      box-shadow: 0 0 4px #2c2c2c;
      transition: opacity 0.2s;
      opacity: 0.8;
    }
  }
  &.btn-type-b {
    padding: 0 1rem;
    color: #fff;
    border: 1px solid #000081;
    background-color: #000081;
    &:hover {
      box-shadow: 0 0 4px rgba(0, 0, 129, 0.4);
      transition: opacity 0.2s;
      opacity: 0.8;
    }
  }
  &.btn-type-c {
    padding: 0 1rem;
    color: #666;
    border: 1px solid #f3f7fa;
    background-color: #f3f7fa;
    &:hover {
      box-shadow: 0 0 4px rgba(197, 197, 197, 0.4);
      transition: opacity 0.2s;
      opacity: 0.8;
    }
  }
}
.bottom-btn-wrap {
  margin-top: 4rem;
  text-align: center;
  .btn-type-a {
    width: 21.1rem;
    height: 6.1rem;
    font-size: 1.8rem;
    line-height: 6.1rem;
    text-align: center;
    border-radius: 5rem;
  }
}

//체크박스
.check-type-a input {
  display: none;
}
.check-type-a input + label {
  font-size: 1.3rem;
  line-height: 2.2rem;
  font-weight: 400;
}
.check-type-a label:before {
  content: "";
  display: inline-block;
  margin: -2px 10px 0 0;
  width: 2.4rem;
  height: 2.4rem;
  border: 1px solid #ccc;
  background-color: #fff;
  box-sizing: border-box;
  border-radius: 0.4rem;
  vertical-align: middle;
}
.check-type-a input:checked + label {
  color: #333 !important;
  font-weight: 600 !important;
}
.check-type-a input:checked + label:before {
  border-color: #000081;
  background: #000081 svgCheck("fff") no-repeat 50% 50%/ 1.6rem;
}

//라디오
.radio-type-a input {
  display: none;
}
.radio-type-a input + label {
  cursor: pointer;
  font-weight: 500;
  color: #333;
}
.radio-type-a label:before {
  content: "";
  display: inline-block;
  margin-right: 10px;
  border-radius: 50%;
  border: 1px solid #bcbbbc;
  vertical-align: middle;
  box-sizing: border-box;
}
.radio-type-a input:checked + label:before {
  background: url(../images/common/ico-radio-bk.png) no-repeat 50% 50%/ 100%;
  border: none;
}

//페이징-번호
.paging {
  margin-top: 5rem;
  display: flex;
  gap: 1rem;
  width: 100%;
  justify-content: center;
}
.paging li {
  position: relative;
  font-size: 1.6rem;
}
.paging li a,
.paging li strong {
  display: block;
  padding: 0 11px;
  width: 100%;
  height: 100%;
  font-size: 1.6rem;
  color: #aaa;
  line-height: 3rem;
}
.paging li.disable {
  border: 1px solid #ccc;
}
.paging li.disable {
  &.paging-left a,
  &.paging-right a {
    background-image: svgPageArrow("ccc");
  }
  &.paging-left-2 a,
  &.paging-right-2 a {
    background-image: svgPageArrow2("ccc");
  }
}
.paging li.disable,
.paging li.disable a {
  pointer-events: none;
}
.paging li strong {
  display: block;
  color: #000081;
  font-weight: 00;
  border-radius: 2rem;
  color: #fff;
  background-color: #000081;
}
.paging li a:hover {
  text-decoration: underline;
}
.paging-left,
.paging-left-2,
.paging-right,
.paging-right-2 {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  border: 1px solid #000081;
}
.paging-left a {
  background: svgPageArrow("000081") no-repeat 50% 50%/ 1rem;
}
.paging-left-2 a {
  background: svgPageArrow2("000081") no-repeat 50% 50%/ 1.5rem;
}
.paging-right a {
  background: svgPageArrow("000081") no-repeat 50% 50%/ 1rem;
  transform: scaleX(-1);
}
.paging-right-2 a {
  background: svgPageArrow2("000081") no-repeat 50% 50%/ 1.5rem;
  transform: scaleX(-1);
}

//로딩바
.loader-wrap {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 99999;
  background-color: rgba(#666, 0.7);
}
.loader-box {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 99999;
  background-color: rgba(#fff, 0.7);
}
.loader {
  --animation-duration: 900ms;
  position: absolute;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100px;
  height: 10px;
  font-size: 0;
  span {
    width: var(--size);
    height: var(--size);
    display: inline-block;
    margin: 0 5px;
    border-radius: 50%;
    background-color: var(--clr-spinner);
    animation: loaderAni var(--animation-duration) ease-in-out infinite;

    @keyframes loaderAni {
      0%,
      100% {
        transform: translateY(75%);
      }

      50% {
        transform: translateY(-75%);
      }
    }
  }

  span:nth-child(1) {
    --clr-spinner: var(--clr1);
    animation-delay: calc(var(--animation-duration) / 6 * -1);
  }
  span:nth-child(2) {
    --clr-spinner: var(--clr2);
    animation-delay: calc(var(--animation-duration) / 6 * -2);
  }

  span:nth-child(3) {
    --clr-spinner: var(--clr3);
    animation-delay: calc(var(--animation-duration) / 6 * -3);
  }
  span:nth-child(4) {
    --clr-spinner: var(--clr4);
    animation-delay: calc(var(--animation-duration) / 6 * -4);
  }
  span:nth-child(5) {
    --clr-spinner: var(--clr5);
    animation-delay: calc(var(--animation-duration) / 6 * -5);
  }
}

//검색박스
.search {
  &-box {
    margin-bottom: 5rem;
    padding: 2.8rem 12.3rem;
    background-color: #f7f7f7;
    border-radius: 2rem;
    .search-form {
      position: relative;
      padding-right: 4rem;
      display: flex;
      background-color: #fff;
      border-radius: 5rem;
      overflow: hidden;
      border-radius: 5rem;
      border: 1px solid #e5e5e5;
      .input-wrap {
        position: relative;
        width: 100%;
      }
      input[type="text"] {
        padding: 0 5.5rem 0 4rem;
        height: 6.4rem;
        font-size: 1.8rem;
        color: #222;
        border: none;
      }
      .btn-input-reset {
        position: absolute;
        display: none;
        top: 0;
        right: 2rem;
        width: 2.3rem;
        height: 100%;
        background: svgInputReset("fff", "aaa");
        background-size: 100% 100%;
      }
    }
    .search-btns {
      display: flex;
      gap: 2rem;
      a {
        width: 2.3rem;
        height: 100%;
      }
      .btn-search {
        background: svgSearch("000") no-repeat 50% 50%/100%;
      }
      .btn-filter {
        background: svgFilter("000") no-repeat 50% 50%/100%;
      }
    }
    &.filter-on {
      .filter-wrap {
        display: block;
      }
      .btn-filter {
        background-image: svgFilter("00B4D6");
      }
    }
    .btn-filter-reset {
      position: absolute;
      top: 0;
      right: 0;
      color: #888;
      font-size: 1.4rem;
    }
    .filter {
      &-wrap {
        position: relative;
        display: none;
        margin: 2.8rem 4rem 0;
      }
      &-title {
        font-size: 2rem;
        font-weight: 600;
      }
      &-list {
        display: flex;
        margin-top: 1.6rem;
        flex-wrap: wrap;
        gap: 0.8rem;
      }
      &-item {
        label {
          display: block;
          padding: 1rem 1.6rem;
          background-color: #fff;
          font-size: 1.4rem;
          border-radius: 5rem;
          transition: background 0.3s ease;
        }
        input:checked + label {
          color: #fff;
          background-color: #00b4d6;
        }
      }
      &-btn {
        margin: 2.8rem -4rem 0;
        border-top: 1px solid #ccc;
        .btn-search {
          display: block;
          margin: 1.6rem auto 0;
          width: 14rem;
          height: 5.1rem;
          background-color: #000081;
          text-align: center;
          color: #fff;
          font-size: 1.6rem;
          line-height: 5.1rem;
          border-radius: 5rem;
        }
      }
    }
  }
}

//bg
.bg {
  &-a {
    background-color: #f7f7f7;
  }
}

//탭
.tab {
  &-type-a {
    display: flex;
    gap: 0.8rem;
    a {
      display: block;
      border: 1px solid #ccc;
      border-radius: 5rem;
      color: #222;
      font-size: 1.4rem;
    }
    .tab-item {
      &.on a {
        background-color: #000081;
        color: #fff !important;
        border-color: #000081;
      }
    }
  }
}

//리스트 (테이블 형식)
.list {
  &-type-a {
    display: flex;
    flex-wrap: wrap;
    gap: 3rem;
    > * {
      position: relative;
      width: calc((100% - 6rem) / 3);
    }
    .info {
      position: absolute;
      top: 0;
      left: 0;
      padding: calc(50 / 460) * 100%;
      width: 100%;
      height: 100%;
      z-index: 1;
    }
    .brand {
      padding: 0 1.6rem;
      display: inline-block;
      height: 4.5rem;
      line-height: 4.5rem;
      border: 1px solid #162da1;
      border-radius: 5rem;
      color: #162da1;
      font-size: 1.8rem;
      font-weight: 700;
    }
    .text-1 {
      color: #666;
    }
  }
  &-type-b {
    display: flex;
    flex-wrap: wrap;
    gap: 3rem;
    justify-content: center;
    > li {
      width: calc((100% - 9rem) / 4);
      border: 1px solid #e5e5e5;
      border-radius: 2rem;
      transition: all 0.3s;
    }
    a[download] {
      display: block;
      padding: 4rem 7rem 4rem 4rem;
      height: 100%;
      font-size: 2.4rem;
      background: svgDownload("162DA1") no-repeat calc(100% - 4rem) calc(100% - 4rem) / 2.4rem;
    }
  }
}

//불릿 리스트
.bullet {
  &-type-a {
    li {
      position: relative;
      display: block;
      width: 100%;
      &:before {
        content: "";
        display: inline-block;
        margin: -0.2rem 1rem 0;
        width: 3px;
        height: 3px;
        border-radius: 50%;
        background-color: #000;
        vertical-align: middle;
      }
    }
  }
}

//엑셀 데이터 테이블
.excel-table {
  table {
    width: 100%;
    font-size: 1.6rem;
    border: 0;
    th,
    td {
      padding: 2.4rem;
      font-weight: 500;
      text-align: center;
    }
    .header td {
      text-align: center;
      background-color: #2f75ff;
      color: #fff;
      border: 1px solid rgba(255, 255, 255, 0.2);
    }
    .cn td {
      color: #666;
      border: 0;
      border-bottom: 1px solid #e5e5e5;
    }
  }
}

//테이블
.table-note {
  margin-top: 2rem;
  text-align: right;
  color: #888;
  white-space: pre-line;
}
.table {
  &-type-a {
    font-size: 1.6rem;
    width: 100%;

    &.bg-w {
      background-color: #fff;
    }

    td,
    th {
      padding: 2.4rem;
      font-weight: 500;
    }
    thead {
      &.line th {
        border: 1px solid rgba(255, 255, 255, 0.2);
      }
      th {
        background-color: #2f75ff;
        color: #fff;
        font-weight: 600;
        text-align: center;
      }
    }
    tbody {
      tr {
        &:first-child {
          th + td {
            border-top: 1px solid #e5e5e5;
          }
        }
        &:last-child {
          td {
            border-color: #ccc;
          }
        }
        &.indent {
          td {
            color: #666;
            font-weight: 400;
          }
        }
      }

      td {
        border-bottom: 1px solid #e5e5e5;
        color: #444;
      }
      .tx-r {
        text-align: right;
      }
      th {
        background-color: #2f75ff;
        color: #fff;
        font-weight: 600;
        text-align: center;
      }
    }
    .down {
      color: #162da1;
      &:after {
        content: "";
        display: inline-block;
        margin-left: 0.6rem;
        width: 9px;
        height: 8px;
        background: svgDownArrow2("162DA1") no-repeat 100% 50% / 100%;
      }
    }
    .up {
      color: #ff4002;
      &:after {
        content: "";
        display: inline-block;
        margin-left: 0.6rem;
        width: 9px;
