//레이어팝업
.layer-popup {
  display: none;
  &:not(.no-dim) {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 2000;
  }
  .popup {
    &-wrap {
      padding: 0 2rem;
      width: 100%;
      height: 100%;
      overflow: auto;
      text-align: center;
      font-size: 0;
      &:before {
        content: "";
        display: inline-block;
        height: 100%;
        width: 0;
        vertical-align: middle;
      }
    }
    &-inner {
      padding: 5.7rem 4rem;
      display: inline-block;
      background-color: #fff;
      border-radius: 2rem;
      vertical-align: middle;
      text-align: left;
    }
    &-head {
      position: relative;
      padding-right: 6.5rem;
      .btn-close {
        position: absolute;
        top: 0;
        right: 0;
        width: 3.6rem;
        height: 3.6rem;
        background-image: svgClose(aaa);
        background-size: 100%;
      }
    }
  }
  &.pop-humanright {
    .popup {
      &-inner {
        padding: 4rem;
        max-width: 50rem;
        width: calc(100% - 3.2rem);
      }
    }

    .humanright {
      &-title {
        letter-spacing: -0.02em;
      }
      &-cont {
        display: block;
        margin-top: 2.4rem;
        padding: 3rem;
        background-color: #f3f7fa;
        width: 100%;
        min-height: 20rem;

        li {
          display: flex;
          align-items: center;
          font-weight: 400;
          font-size: 1.6rem;
          line-height: 2.8rem;
          letter-spacing: -0.02em;
          color: #666;
          align-items: flex-start;

          &::before {
            content: "";
            display: inline-block;
            width: 0.3rem;
            height: 0.3rem;
            background-color: #666;
            border-radius: 50%;
            margin: 1.1rem;
            flex-shrink: 0;
          }
        }
      }
    }
  }
  &.pop-workplace {
    position: absolute;
    right: 0;
    bottom: -2.8rem;
    transform: translateY(100%);

    .popup {
      &-inner {
        padding: 4rem;
        width: 70.5rem;
        box-shadow: 1rem 1rem 3.2rem rgba(34, 34, 34, 0.4);
        overflow: visible;
      }
      &-wrap {
        width: auto;
        height: auto;
        padding: 0;
        overflow: visible;

        &::before {
          display: none;
        }
      }
    }

    .workplace {
      &-title {
        line-height: 3.8rem;
      }
      &-cont {
        display: grid;
        width: 46rem;
        margin: 3.2rem auto 0;
        grid-template-columns: 1fr 1fr 1fr;
        column-gap: 11rem;
        row-gap: 4rem;

        li {
          width: 100%;
        }

        a {
          width: 100%;
        }

        .ico {
          width: 100%;
          img {
            width: 100%;
          }
        }

        .name {
          font-size: 1.4rem;
          line-height: 1.67rem;
          text-align: center;
          font-weight: 400;
          display: block;
          margin-top: 0.8rem;
          color: #2f75ff;
          text-decoration: underline;
        }
      }
    }
  }
}

@media (hover: hover) and (pointer: fine) {
