/* 기본 */
@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.8/dist/web/static/pretendard.css");
* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Pretendard Variable", Pretendard, Roboto, "Noto Sans KR", "Segoe UI", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
  word-break: keep-all;
}

pre {
  font-family: inherit;
  margin: 0;
}

html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

xmp,
form,
input,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
dl,
dt,
dd {
  margin: 0;
  padding: 0;
}

ul,
ol,
li {
  list-style: none;
  appearance: none;
}

img {
  max-width: 100%;
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

th,
td {
  padding: 0;
}

textarea {
  padding: 0;
  margin: 0;
  line-height: 1.4;
}

fieldset {
  display: block;
  border: none;
  padding: 0;
  margin: 0;
}

legend,
caption {
  width: 0;
  height: 0;
  padding: 0;
  margin: 0;
  font-size: 0;
  line-height: 0;
}

a:link {
  color: inherit;
  text-decoration: none;
}

a:visited {
  color: inherit;
  text-decoration: none;
}

a:hover {
  color: inherit;
  text-decoration: none;
}

a:active {
  color: inherit;
  text-decoration: none;
}

em {
  font-style: normal;
}

::-ms-clear {
  display: none;
}

input {
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  appearance: none;
}

input[type=text],
input[type=password],
input[type=number],
input[type=tel],
input[type=email],
input[type=search] {
  display: block;
  width: 100%;
  -webkit-appearance: none;
  border: 1px solid #ddd;
  font-family: inherit;
  font-size: 1.8rem;
  color: #222;
  background-color: #fff;
  transition: 0.4s border;
  border-radius: 0.4rem;
  font-size: 1.8rem;
}
input[type=text].error,
input[type=password].error,
input[type=number].error,
input[type=tel].error,
input[type=email].error,
input[type=search].error {
  color: red;
}
input[type=text]:focus,
input[type=password]:focus,
input[type=number]:focus,
input[type=tel]:focus,
input[type=email]:focus,
input[type=search]:focus {
  border: 1px solid #2f75ff !important;
  outline: none;
}

textarea {
  width: 100%;
  resize: none;
  border: 1px solid #ddd;
  -webkit-appearance: none;
  font-family: inherit;
  transition: 0.4s border;
  border-radius: 0.4rem;
  font-size: 1.8rem;
}
textarea.error {
  color: red;
}
textarea:focus {
  border: 1px solid #2f75ff !important;
  outline: none;
}

select {
  width: 100%;
  padding-right: 5rem;
  outline: none;
  border: 1px solid #ddd;
  font-size: 1.8rem;
  font-family: inherit;
  line-height: 1.6;
  background: #fff svgDownArrow("222") no-repeat calc(100% - 2.4rem) 50%/11px;
  border-radius: 0.4rem;
  box-shadow: none;
  vertical-align: middle;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
}

select:focus {
  border: 1px solid #000;
  color: #000;
}

select:disabled {
  opacity: 0.5;
}

select option {
  color: #999;
}

select option:first-child {
  color: #555;
}

select::-ms-expand {
  display: none;
}

option::-moz-selection {
  background: #e0e3e5;
}

option::selection {
  background: #e0e3e5;
}

option:selected {
  background: rgb(224, 227, 229);
}

button {
  margin: 0;
  padding: 0;
  outline: 0;
  background: transparent;
  border: none;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 0;
}

/*scroll*/
body {
  scrollbar-width: thin;
  scrollbar-color: #89898a transparent;
}

body::-webkit-scrollbar {
  width: 4px;
}

body::-webkit-scrollbar-track {
  background-color: #e5e5e5;
}

body::-webkit-scrollbar-thumb {
  background-color: #89898a;
  border-radius: 10px;
}

body::-webkit-scrollbar-thumb:hover {
  background: #555;
}

body::-webkit-scrollbar-button:start:decrement,
::-webkit-scrollbar-button:end:increment {
  display: none;
}

::-webkit-scrollbar {
  width: 4px;
  height: 0;
}

::-webkit-scrollbar-track {
  background-color: #e5e5e5;
}

::-webkit-scrollbar-thumb {
  background-color: #89898a;
  border-radius: 0;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

::-webkit-scrollbar-button:start:decrement,
::-webkit-scrollbar-button:end:increment {
  display: none;
}

